import { intervalToDuration } from 'date-fns';

export function getTimeDiffStr(date1, date2) {
  const diff = intervalToDuration({
    start: date1,
    end: date2,
  });
  const { years, months, days, hours, minutes, seconds } = diff;
  if (years > 0) {
    return `${years}`;
  }
  if (months > 0) {
    return `${months}`;
  }
  if (days > 0) {
    return `${days}`;
  }
  if (hours > 0) {
    return `${hours}`;
  }
  if (minutes > 0) {
    return `${minutes}`;
  }
  if (seconds > 0) {
    return `${seconds}`;
  }
  return '';
}
